const aa = require('../assets/NotoSansTC-Medium.ttf')

Font.register({ family: 'NotoSansTC', src: aa })
// 中文換行問題
Font.registerHyphenationCallback((word) => {
  const isEn = (temp) => {
    let reg = new RegExp('[A-Za-z]+')
    if (reg.test(temp)) {
      return true
    }
    return false
  }

  if (word.length === 1 || isEn(word)) {
    return [word]
  }

  return Array.from(word)
    .map((char) => [char, ''])
    .reduce((arr, current) => {
      arr.push(...current)
      return arr
    }, [])
})

