import zlib from 'browserify-zlib'
const Buffer = require('buffer/').Buffer

const priceFormat = (value) => {
  if (value) {
    value = value.toString()
    value += ''
    var arr = value.split('.')
    var re = /(\d{1,3})(?=(\d{3})+$)/g

    return arr[0].replace(re, '$1,') + (arr.length === 2 ? '.' + arr[1] : '')
  }
  return ''
}
const renderStatusText = (status) => {
  switch (status) {
    case 'A':
      return '編輯中'
    case 'B':
      return '審核中'
    case 'C':
      return '已核准'
    case 'D':
      return '已成案'
    case 'E':
      return '終止中'
    default:
      return ''
  }
}

const decodeArrayBuffer = (arrayBuffer) => {
  const buffer = Buffer.from(arrayBuffer)
  const jsonFileBuffer = zlib.gunzipSync(buffer)

  const bufferWithoutBOM = removeBOM(jsonFileBuffer)

  const jsonString = bufferToString(bufferWithoutBOM)

  const jsonObject = JSON.parse(jsonString)
  return jsonObject
}

const bufferToString = (buffer) => {
  let jsonString = ''
  for (const byte of buffer) {
    jsonString += String.fromCharCode(byte)
  }
  return jsonString
}

const removeBOM = (buffer) => {
  if (buffer.length >= 3 && buffer[0] === 0xef && buffer[1] === 0xbb && buffer[2] === 0xbf) {
    return buffer.slice(3)
  } else {
    return buffer
  }
}

export { priceFormat, renderStatusText, decodeArrayBuffer }
